.stars {
  display: flex;
  align-items: center;

  &__star {
    height: 16px;
    width: 16px;
    background-position: 50%;
    background-repeat: no-repeat;
    background-image: url("../images/star.svg");

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  @for $n from 0 through 5 {
    &--#{$n} &__star:nth-child(-n + #{$n}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
